<!-- 启动引导页 -->
<template>
	<view class="welcome-container">
		<view class="logo animate__animated animate__fadeInDown">
			<image :src="logoUrl" class="logo-img"></image>
		</view>
		
		<view class="animate__animated animate__heartBeat" v-if="!hasLogin" @click="login">{{ hint }}</view>
		
		<view class="welcome animate__animated animate__fadeInDown" v-else>
			<text>欢迎，</text>
			<text>{{ nickName }}</text>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				logoUrl: '/static/logo.jpg',
				hint: '阿圆便利店',
				nickName: 'tzw',
				hasLogin: false,
				reqireLoginTimer: undefined,
				navigateTimer: undefined
			};
		},
		
		methods: {
			login() {
				this.hasLogin = true
				this.navigateTimer = setTimeout(() => {
					uni.navigateTo({
						url: '../index/index'
					})
				} , 1000)
			}
		},
		
		onLoad() {
			this.reqireLoginTimer = setTimeout(() => {
				this.hint = '点击立即登录'
			} , 1000)
		},
		
		onUnload() {
			console.log(123)
			clearTimeout(this.reqireLoginTimer)
			clearTimeout(this.navigateTimer)
		}
	}
</script>

<style lang="scss">
	.welcome-container {
		width: 100vw;
		height: 100vh;
		font-weight: bolder;
		font-size: 36rpx;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		background-image: url('@/static/background.png');
	}
	
	
	.logo {
		width: 200rpx;
		height: 200rpx;
		margin-bottom: 20rpx;
		border: $uni-border;
		border-radius: $uni-border-radius-circle;
		overflow: hidden;
	}
	
	.logo-img {
		width: 100%;
		height: 100%;
	}
</style>
